<template>
  <div>
    <nav>
      <input type="checkbox" id="check" />
      <label for="check" class="checkbtn">
        <i class="pi pi-bars"></i>
      </label>
      <label class="logo">Tareas</label>
      <ul>
        <li><router-link class="active" to="/">Inicio</router-link></li>
        <li><router-link to="/tareas">Tareas</router-link></li>
        <li><router-link to="/usuarios">Usuarios</router-link></li>
        <li><a href="#" @click="logout">Salir</a></li>
      </ul>
    </nav>
  </div>
</template>
<script>
import LoginService from '../../service/LoginService';

export default {
  data: () => ({
    loginServices: null
  }),
  name: "navigation",
  created(){
    this.loginServices = new LoginService();
  },
  methods: {
    logout() {
      this.loginServices.logout().then(() => {
        localStorage.removeItem("user-token");
        localStorage.removeItem("user-name");
        window.location.href = "/";
      });
    },
  },
};
</script>
